import wPicker from "@/components/w-picker/w-picker.vue";
export default {
	components: {
		wPicker
	},
	data() {
		return {
			columns: [],
			clubName: '余额',
			money: '',
			walletId: '',
			headImg: '',
			userName: ''
		}
	},
	created() {
		this.$http.get({
			url: '/v1/rest/userwallet/userRecharge',
			data: {
				userId: uni.getStorageSync('userInfo').userId
			}
		}).then(resp => {
			// console.log(resp)
			if(resp.status == 200) {
				this.walletId = resp.data.userWalletId
				this.headImg = resp.data.headPortrait
				this.userName = resp.data.account
			}
		})
	},
	methods: {
		inputMoney(v) {
			this.money = v.detail
		},
		submit() {
			const that = this
			if(Number(this.money) <= 0) {
				uni.showToast({
					title: '请输入您要充值的金额',
					duration: 2000,
					icon: 'none'
				});
			}else{
				this.$http.post({
					url: '/v1/rest/pay/pay',
					data: {
						userId: uni.getStorageSync('userInfo').userId,
						totalPrice: Number(this.money),
						clubOrUserWalletId: this.walletId,
						type: 0  //与后端约定钱包type为0，会费充值type为1
					}
				}).then(resp => {
					// console.log(resp)
					if(resp.statusCode == 200) {
						uni.login({
						  provider: 'weixin',
						  success: function (loginRes) {
						    that.$http.get({
						    	url: '/v1/rest/login/updateOpenId',
						    	data:{
						    		code: loginRes.code,
										userId: uni.getStorageSync('userInfo').userId
						    	}
						    }).then(resp => {
									// console.log(resp)
									that.submit()
								})
						  }
						});
					}else if(resp.status == 200) {
						this.orderNo = resp.data.orderNo
						uni.requestPayment({
							provider: 'wxpay',
							timeStamp: resp.data.timeStamp,
							nonceStr: resp.data.nonceStr,
							package: resp.data.package,
							signType: resp.data.signType,
							paySign: resp.data.paySign,
							success: function (res) {
								// console.log(res)
								// 支付成功回调
								that.$http.get({
									url: '/v1/rest/pay/memberWechatPayCallback',
									data: {
										type: 'success',
										orderNo: that.orderNo,
										rechargeType: 0
									}
								}).then(resp => {
									// console.log(resp)
									if(resp.status == 200) {
										uni.showToast({
											title: resp.data.message,
											duration: 2000,
											icon: 'none'
										}); 
										uni.navigateBack()
									}
								})
							},
							fail: function (err) {
								// console.log(err)
								uni.showToast({
									title: '支付取消！',
									duration: 2000,
									icon: 'none'
								});
								// 支付取消回调
								that.$http.get({
									url: '/v1/rest/pay/memberWechatPayCallback',
									data: {
										type: 'fail',
										orderNo: that.orderNo,
										rechargeType: 0
									}
								}).then(resp => {
									// console.log(resp)
									if(resp.status == 200) {
										uni.showToast({
											title: resp.data.message,
											duration: 2000,
											icon: 'none'
										});
									}
								})
							}
						});
					}
				})
			}
		}
	}
}
